import React, { useState, useEffect } from 'react';
import { Select } from 'antd';
import { collection, getDocs } from 'firebase/firestore';
import { PiSlidersHorizontalBold } from "react-icons/pi";
import { db } from '../../config';
import { useLocation, useNavigate } from 'react-router-dom';

const { Option } = Select;

const SearchMobile = ({ handleOpenDrawer }) => {
    const [locations, setLocations] = useState([]);
    const [desiredAddressCode, setDesiredAddressCode] = useState('');
    const [selectedLocation, setSelectedLocation] = useState('all');
    const location = useLocation();
    const navigate = useNavigate();

    useEffect(() => {
        const fetchLocations = async () => {
            try {
                const jobsCollection = collection(db, 'jobs');
                const jobsSnapshot = await getDocs(jobsCollection);
                const jobsData = jobsSnapshot.docs.map(doc => doc.data());
                const uniqueLocations = [...new Set(jobsData.flatMap(job => job.locations || []))];
                const parsedLocations = uniqueLocations.map((location, index) => {
                    if (location.includes('(')) {
                        const [mainName, districts] = location.split('(');
                        return {
                            code: index,
                            name: mainName.trim(),
                            districts: districts.slice(0, -1).split(', ')
                        };
                    }
                    return { code: index, name: location.trim() };
                });
                const uniqueParsedLocations = Array.from(
                    new Map(parsedLocations.map(location => [location.name.toLowerCase(), location])).values()
                );

                setLocations(uniqueParsedLocations);
            } catch (error) {
                console.error("Error fetching locations:", error);
            }
        };

        fetchLocations();

        const searchParams = new URLSearchParams(location.search);
        const query = searchParams.get('query') || '';
        const desiredAddressCode = searchParams.get('desiredAddressCode') || 'all';

        setDesiredAddressCode(query);
        setSelectedLocation(desiredAddressCode);
    }, [location]);

    const handleSearch = (locationValue = selectedLocation) => {
        const locationParam = locationValue === 'all' ? '' : `&desiredAddressCode=${locationValue}`;
        navigate(`/tim-kiem-viec-lam?query=${desiredAddressCode}${locationParam}`);
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleSearch();
        }
    };

    const handleLocationChange = (value) => {
        setSelectedLocation(value);
        handleSearch(value);
    };

    return (
        <>
            <div className="job-list__search-box-mb">
                <div className="job-list__search-input-mb">
                    <input
                        placeholder="Việc làm, vị trí ứng tuyển..."
                        value={desiredAddressCode}
                        onChange={(e) => setDesiredAddressCode(e.target.value)}
                        onKeyPress={handleKeyPress}
                    />
                </div>
                <div className="job-list__search-select-mb">
                    <Select
                        value={selectedLocation}
                        style={{ width: '100%' }}
                        onChange={handleLocationChange}
                        showSearch
                        className="custom-selectss job-list__search-select-mb-value text-ellipsis"
                    >
                        <Option value="all">Tất cả</Option>
                        {locations.map((location) => (
                            <Option key={location.code} value={location.name}>{location.name}</Option>
                        ))}
                    </Select>
                </div>
                <div className="job-list__search-filter-mb" onClick={handleOpenDrawer}>
                    <span>
                        <PiSlidersHorizontalBold size={24} />
                    </span>
                </div>
            </div>
        </>
    );
};

export default SearchMobile;